
import hot from '../Assemble/hotness/index'
import Swiper from 'vux/src/components/swiper/swiper.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Popup from 'vux/src/components/popup/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import InlineXNumber from 'vux/src/components/inline-x-number/index.vue'

export default {
directives: {
TransferDom
},
props:['popup'],
components:{
hot,Swiper,Popup, XButton,InlineXNumber
},
computed:{

goodId(){
return this.$route.query.id;
},

popupshow:{
get:function () {
return this.$parent.show;
},
set: function (oldVal,newVal) {
}
},
buyNum:{
get:function () {
return this.$parent.buyNum;
},
set: function (Val) {
this.form.num=Val;
}

},
type(){
return localStorage.getItem('type');
}
},
data() {
return {
imgList: [








],
arr:[],
param:{openId:localStorage.getItem("openId"),id:this.goodId},
num:1,

goodsDetail:{},
goodsDetailImg:[],
specList:[],
spec:'',
form:{
openId:localStorage.getItem("openId"),
id:'',
num:1,
specList:[]
}
}
},
created(){
settitle('商品详情');
},
methods: {

close(){
this.$parent.show = false;
},
onIndexChange(currentIndex) {
this.num = currentIndex + 1;
},

buyGoods(){
this.form.id = this.goodId;
this.$parent.show = false;
if(this.$parent.title == 'cart'){


if(this.goodsDetail.state!=0){
this.$vux.toast.text("该商品已下架");
return
}
this.addCart();
}else{
if(this.goodsDetail.state!=0){
this.$vux.toast.text("该商品已下架");
return
}

var obj = {
openId:localStorage.getItem("openId"),
goodList:[{
id:this.goodsDetail.id,
num:this.form.num
}]
}
obj = JSON.stringify(obj);
this.$router.push('/paysure?data='+obj)
}
},

addCart(){
this.$fetch.post("fruits/app/cart/joinCart",this.form).then(res =>{

this.$parent.buyNum  =this.$parent.buyNum/1;
this.$parent.buyNum += this.form.num/1;
localStorage.setItem('catnum',this.$parent.buyNum)

})
},

getGood(){

this.$fetch.post("fruits/app/blank/getGoodInfo",{openId:localStorage.getItem("openId"),id:this.goodId}).then(res =>{

console.log(res,999);
res.obj.imgBannerList.forEach((el,i) => {

this.imgList.push({
url: 'javascript:',
img:"http://www.gsb.yuntunet.cn/fruits/app/blank/showPicture?attachmentId="+el,
title:'123'
});



});


this.goodsDetail = {...res.obj};
this.goodsDetail.price = this.goodsDetail.price.toFixed(2);
this.goodsDetail.original = this.goodsDetail.original.toFixed(2);
var a = JSON.stringify({a:1})
})
},

getSpecs(){
this.$fetch.post("fruits/app/cart/getGoodsSpec",{openId:localStorage.getItem("openId"),id:this.goodId}).then(res =>{
this.specList = [...res.obj.goodsSpecs];
this.specList.forEach((e,ind) =>{
this.form.specList.push({
value:null,
specName:e.name,
specId:e.id,
check:false
})
e.valueList.forEach((el,i) =>{
if(i == 0){
this.form.specList[ind].value = el;
}

})
})
console.log(this.form.specList,888)

})
},

choseSpec(item,index,spec){
this.spec = index;
console.log(index)
this.form.specList[index].specName = item.name;
this.form.specList[index].value = spec;
this.form.specList[index].specId = item.id;
},

},
mounted() {
this.$nextTick(() =>{
this.getGood();
})

this.getSpecs();
},
updated(){

}
}



